@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.btn {
  display: inline-flex;
  padding: 15px 25px;
  color: #f2f5f6;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--white {
  background-color: transparent;
  color: #00b2ff;
  font-weight: 700;
  border: 1px solid #00b2ff;
  border-radius: 3px;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header {
  background: linear-gradient(#041827 84.9%, rgba(4, 24, 39, 0) 100%);
  color: #f2f5f6;
  font-family: "PT Mono", "Arial", sans-serif;
  padding: 30px 0;
  position: sticky;
  top: 0;
  width: 100%;
  z-index: 2;
}

.header__wrapper {
  display: flex;
  margin: 0 auto;
  align-items: center;
  justify-content: space-between;
  max-width: 1164px;
}

.header__logo {
  margin-right: 20px;
}

.hedaer__logo-img {
  width: 215px;
  height: 55px;
}

.header__contacts {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  align-items: center;
}

.header__contact {
  width: 225px;
}
.header__contact:not(:last-child) {
  margin-right: 30px;
}

.header__link {
  color: #f2f5f6;
}

.header__icon {
  position: relative;
  top: 5px;
  fill: #00b2ff;
}

.header__button {
  background-color: transparent;
  padding: 10px 15px;
  border: 1px solid #f2f5f6;
  border-radius: 3px;
  align-items: center;
  margin-right: 60px;
}

@media (max-width: 1023px) {
  .header__button,
.header__link span {
    display: none;
  }
  .header {
    position: static;
    padding: 20px 70px 25px 50px;
  }
  .header__contact {
    width: auto;
    padding: 10px;
  }
  .header__contact:not(:last-child) {
    margin-right: 20px;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 10px 35px 20px 25px;
  }
}
.footer {
  background-color: #041827;
  color: rgba(255, 255, 255, 0.7);
}

.footer__container {
  max-width: 1220px;
  margin: 0 auto;
  padding: 50px 0 55px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.footer__logo {
  display: block;
  margin-bottom: 30px;
}

.footer__title {
  margin: 0 0 20px;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.75px;
  font-weight: 400;
  text-transform: uppercase;
  color: #ffffff;
}

.footer__contacts {
  max-width: 345px;
}

.footer__address {
  font-style: normal;
  margin-bottom: 10px;
}

.footer__phones {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.footer__phone {
  margin-bottom: 10px;
}

.footer__phone-link {
  color: rgba(255, 255, 255, 0.7);
}

.footer__accordeon {
  display: none;
}

@media (max-width: 1023px) {
  .footer__container {
    padding: 50px 45px 65px 50px;
  }
  .footer__wrapper {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 35px;
  }
}
@media (max-width: 767px) {
  .footer__container {
    padding: 35px 0;
    flex-direction: column;
  }
  .footer__wrapper {
    flex-direction: column;
    align-items: center;
    position: relative;
    margin-bottom: 0;
  }
  .footer__wrapper::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: rgba(248, 253, 255, 0.7);
  }
  .footer__logo {
    margin-bottom: 20px;
  }
  .footer__socials {
    margin-bottom: 35px;
  }
  .footer__nav {
    padding: 20px 25px;
    position: relative;
  }
  .footer__nav::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: rgba(248, 253, 255, 0.7);
  }
  .footer__nav-list {
    display: none;
    margin-top: 10px;
  }
  .footer__title {
    margin-bottom: 0;
  }
  .footer__accordeon {
    display: block;
    position: absolute;
    right: 26px;
    top: 20px;
    width: 15px;
    height: 15px;
    border: none;
    background-color: transparent;
  }
  .footer__accordeon::before, .footer__accordeon::after {
    content: "";
    width: 100%;
    height: 1px;
    background-color: #f8fdff;
    position: absolute;
    top: 50%;
    left: 0;
  }
  .footer__accordeon::before {
    transform: rotate(90deg);
  }
  .footer__accordeon--active::before {
    content: none;
  }
  .footer__contacts {
    padding: 20px 25px;
    max-width: none;
    position: relative;
  }
  .footer__contacts::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: rgba(248, 253, 255, 0.7);
  }
  .footer__address {
    margin-top: 20px;
  }
}
.social-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
}

.social-list__item:not(:last-child) {
  margin-right: 25px;
}

.social-list__icon {
  fill: #f8fdff;
}

.nav-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 220px 220px;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  grid-auto-flow: column;
  gap: 10px 30px;
}

.nav-list__item {
  max-width: 220px;
}

.nav-list__link {
  color: rgba(255, 255, 255, 0.7);
  font-size: 16px;
  line-height: 22px;
}

@media (max-width: 1023px) {
  .nav-list {
    display: flex;
    flex-direction: column;
  }
}
.promo {
